  margin-top: -20px;
}

@media only screen and (max-width: 600px) {
  .jumbotron h2 {
    margin-top: 10px;
  }
}

/**
 * jumbotron animation
 */

.jumbotron {
  height: 180px;
  color: #fff;
  background: -webkit-linear-gradient(135deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.slider {
	position: relative;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 400px;
	height: 300px;
	box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
	margin-top: 200px;
}

.wrapper {
	overflow: hidden;
	position: relative;
	width: 400px;
	height: 300px;
	z-index: 1;
}

.slides {
	display: flex;
	position: relative;
	top: 0;
	left: -400px;
	width: 10000px;
}

.slides.shifting {
	-webkit-transition: left 0.2s ease-out;
	transition: left 0.2s ease-out;
}

.slide {
	width: 400px;
	height: 300px;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	justify-content: center;
	-webkit-transition: all 1s;
	transition: all 1s;
	position: relative;
	background: white; /*#080808;*/
	border-radius: 2px;
}

.slider.loaded .slide:nth-child(2),
.slider.loaded .slide:nth-child(7) {
	background: #ffcf47;
}
.slider.loaded .slide:nth-child(1),
.slider.loaded .slide:nth-child(6) {
	background: #7adcef;
}
.slider.loaded .slide:nth-child(3) {
	background: #3cff96;
}
.slider.loaded .slide:nth-child(4) {
	background: #a78df5;
}
.slider.loaded .slide:nth-child(5) {
	background: #ff8686;
}

.control {
	position: absolute;
	top: 50%;
	width: 50px;
	height: 50px;
	background: #fff;
	border-radius: 50px;
	margin-top: -20px;
	box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
	z-index: 2;
}

.prev,
.next {
	background-size: 22px;
	background-position: center;
	background-repeat: no-repeat;
	cursor: pointer;
}

.prev {
	background-image: url(https://cdn0.iconfinder.com/data/icons/navigation-set-arrows-part-one/32/ChevronLeft-512.png);
	left: -20px;
}

.next {
	background-image: url(https://cdn0.iconfinder.com/data/icons/navigation-set-arrows-part-one/32/ChevronRight-512.png);
	right: -20px;
}

.prev:active,
.next:active {
	-webkit-transform: scale(0.8);
	        transform: scale(0.8);
}

.notifications {
	color: brown;
}

.pointer {
	cursor: pointer;
}

.cursor {
	cursor: pointer;
}
.bg-new {
	background-color: #ffd5d5;
	margin: 5px;
}

